import React from 'react';
import 'antd-mobile/dist/antd-mobile.css';
import Home from './Taobao/Home'
import Cart from './Taobao/Cart'
import List from './Taobao/List'
import My from './Taobao/My'
import Ser from './Taobao/Ser'
import Goodslist from './Taobao/Goodslist'
import Lan from './Taobao/Lan'
import Kuzi from './Taobao/Kuzi'
import Yun from './Taobao/Yun'
import Maoyi from './Taobao/Maoyi'
import Mian from './Taobao/Mian'
import Mfu from './Taobao/Mfu'
import Mu from './Taobao/Mu'
import Mao from './Taobao/Mao'
import Tao from './Taobao/Tao'
import Can from './Taobao/Can'
import {BrowserRouter as Router,Route,Switch,Redirect} from 'react-router-dom';

const App=()=>{
  return <Router> 
      <Switch>
        <Route path='/home' component={Home}/>
        <Route path='/cart' component={Cart}/>
        <Route path='/list' component={List}/>
        <Route path='/my' component={My}/>
        <Route path='/ser' component={Ser}/>
        <Route path='/goodslist' component={Goodslist}/>
        <Route path='/lan' component={Lan}/>
        <Route path='/kuzi' component={Kuzi}/>
        <Route path='/yun' component={Yun}/>
        <Route path='/lan' component={Lan} />
        <Route path='/maoyi' component={Maoyi} />
        <Route path='/mian' component={Mian} />
        <Route path='/mfu' component={Mfu} />
        <Route path='/mu' component={Mu} />
        <Route path='/mao' component={Mao} />
        <Route path='/tao' component={Tao} />
        <Route path='/can' component={Can} />
        {/* <Route render={()=><Redirect to='/home' />}/> */}
        <Route component={Home} />
    </Switch>
  </Router>
}
export default App;









